/* @jsx jsx */
import {jsx} from "@emotion/react"

import * as styles from "./ListRow.styles";
import {ProjectItemProps} from "../ProjectItemProps";

const NameOrLink = (props: { name: string, url?: string }): JSX.Element => {
    const {name, url} = props;
    if (name && url) {
        return <a href={url}>{name}</a>
    }
    return <div>{name}</div>
}

const ListRow = (props: ProjectItemProps) => {
    const {name, url, institutionName, institutionUrl, tags} = props;

    let tagsComponent = null;
    if (tags) {
        tagsComponent = <div>
            {tags.map(tag => {
                return <div css={styles.tag}>{tag}</div>
            })}
        </div>
    }

    return (<div css={styles.container}>
        <div css={styles.bulletCell}>-</div>
        <div>
            <div>
                <NameOrLink name={name} url={url}/>
            </div>
            <div>
                <i>
                    <NameOrLink name={institutionName} url={institutionUrl}/>
                </i>
            </div>
            <div>
                {tagsComponent && tagsComponent}
            </div>
        </div>
    </div>)
}

export default ListRow;
